import Vue from 'vue'
import App from './App.vue'
import { Card, Link, Badge, Divider } from 'element-ui';
import './assets/my-style.css'
import cloudbase from "@cloudbase/js-sdk"

// 应用UI
Vue.component(Card.name, Card);
Vue.component(Link.name, Link);
Vue.component(Badge.name, Badge);
Vue.component(Divider.name, Divider);

Vue.config.productionTip = false

function signIn() {
  // 链接腾讯云
  const cloud = Vue.prototype.cloud = cloudbase.init({
    // env: "xxx",
    env: "xmrl-prod-jawjt",
    // env: "xmrl-y0wtp"
  })
  const cloudAuth = Vue.prototype.cloudAuth = cloud.auth({
    persistence: 'local'
  })
  // 避免重复登录
  if (cloudAuth.hasLoginState()) {
    console.log('无需重复登录')
    return Promise.resolve()
  }
  return cloudAuth.anonymousAuthProvider()
    .signIn()
    .then((item) => {
      console.log("登录成功", item);
      return Promise.resolve()
    })
    .catch((error) => {
      console.error("登录失败", error);
      return Promise.reject();
    });
}

signIn().then(() => {
  // 链接成功后再实例化app
  new Vue({
    render: h => h(App),
  }).$mount('#app')
})
